<template>
    <div class="hello">
        <h1>索引目录</h1>

        <Menu theme="dark" @on-select="selectMenu">
            <Submenu name="1">
                <template slot="title">
                    <Icon type="ios-paper"/>
                    主要内容
                </template>
                <MenuItem name="home">首页</MenuItem>
                <MenuItem name="shortcutMenu">快捷菜单</MenuItem>
            </Submenu>
        </Menu>
    </div>
</template>

<script>
    export default {
        name: 'EntryIndex',
        data () {
            return {}
        },
        methods: {
            selectMenu (name) {
                this.$router.push(name);
            }
        }
    }
</script>

<style scoped>
</style>



